.home-text-container {
  margin: 150px auto;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

.home-container {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.left,
.right {
  flex-basis: 50%;
}

.container,
.profile-container {
  border: 0.2px solid #e6e4e3;
  padding: 40px;
  border-radius: 10px;
  text-align: center;
  width: 90%;
  margin: 20px;
}

/* .bar{
  width:800px;
} */

.container-pie {
  border: 0.2px solid #e6e4e3;
  padding: 40px;
  border-radius: 10px;
  text-align: center;
  width: 90%;
  margin: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.profile-container {
  max-width: 600px;
}

.title {
  font-weight: bold;
  font-size: 30px;
  margin-bottom: 10px;
}

.profile-container .title {
  text-align: left;
}

.message {
  font-size: 18px;
  margin-bottom: 20px;
}

.register-button {
  font-size: 1rem;
  margin-top: 1.5rem;
  padding: 10px 0;
  border-radius: 20px;
  outline: none;
  border: none;
  width: 30%;
  color: #fff;
  cursor: pointer;
  background-color: #333;
}

.register-button:hover {
  background-color: #555;
}

.register-button:focus {
  outline: none;
}

.profile-row {
  display: flex;
  flex-direction: row;
  margin-top: 10px;
  text-align: left;
}

.profile-row .left {
  font-weight: bold;
  flex-basis: 45%;
}

.profile-container .right {
  flex-basis: 55%;
}

@media screen and (max-width: 700px) {
  .home-text-container {
    margin: 20px auto;
  }

  .home-container {
    flex-direction: column;
    gap: 0;
  }

  .container,
  .profile-container {
    padding: 20px;
    width: 90%;
  }

  .title {
    font-size: 24px;
  }

  .message {
    font-size: 16px;
  }

  .register-button {
    margin-top: 1.2rem;
  }

  .profile-row .right {
    font-size: 13px;
  }

  .bar {
    max-width: 600px;
    width: calc(100% - 10px);
  }
}

@media screen and (max-width: 400px) {
  .bar {
    width: calc(100% - 10px);
  }
}
select {
  display: block;
  outline: none;
  border: 0.2px solid #c8c5c4;
  width: 90%;
  padding: 10px;
  margin: 10px auto;
  border-radius: 10px;
}

.recharts-wrapper {
  display: flex;
  flex-direction: row;
}

.api-result p{
  margin-top: 20px;
  font-size: 14px;
}

.api-result .icon{
  margin-left: 10px;
  font-size: 16px;
  color: dimgray;
}

.api-result img {
  width: 300px;
  margin-top: 10px auto;
}
